<template>
  <div class="slide">
    <transition name="slide">
      <div v-show="slideShow" class="slide-main">
        <slot>
          <div class="slide-main-top">
            <img src="../assets/images/avatar.jpg" alt="">
            <span>Jo's</span>
          </div>
          <div class="slide-main-bottom">
            <div class="title">Favourite
              <ul>
                <li>Movie</li>
                <li>Music</li>
                <li>Book</li>
                <li>Photo</li>
              </ul>
            </div>
            <div class="title">Setting
              <ul>
                <li>History</li>
                <li>Mode</li>
                <li>Nick</li>
                <li>Avatar</li>
              </ul>
            </div>
          </div>
        </slot>
      </div>
    </transition>
    <transition name="slide2">
      <div v-show="slideShow" @click="close" class="slide-side"></div>
    </transition>
  </div>
</template>

<script>

export default {
  props:{
    slideShow:{
      type:Boolean,
      default:false
    }
  },
  methods:{
    close:function(){
      this.$emit("closeSlide")
    }
  }
}
</script>

<style scoped>
.slide-main{
  width:80%;
  height:100%;
  position:fixed;
  overflow: hidden;
  top:0;
  z-index:10002;
}
.slide-side{
  top:0;
  z-index:10001;
  position:absolute;
  width:100%;
  height:100%;
  background-color:rgba(0,0,0,.45);
}
.slide-main-top{
  box-sizing: border-box;
  padding: 5px;
  height: 50px;
  background:#382a67;
  color:white;
  line-height:40px;
}
.slide-main-top img{
  display: block;
  float: left;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}
.slide-main-top span{
  left: 50%;
  position: absolute;
}
.slide-main-bottom{
  width: 100%;
  height: 100%;
  background:#2a2150;
  color:white;
  font-size: 16px;
  box-shadow: 0 0 0 5px #271e49 inset;
}
.slide-main-bottom .title{
  box-sizing: border-box;
  padding: 10px 0;
  text-align: left;
  text-indent: 20px;
  font-size: 14px;
  font-weight: 800;
  color: #883ea5;
}
li{
  height: 45px;
  line-height: 45px;
  width: 100%;
  text-indent: 40px;
  font-size: 16px;
  font-weight: 700;
  color: whitesmoke;
  transition: all .3s;
}
li:hover{
  background-color: #4c7be3;
}
.slide-enter{
  transform:translateX(-400px);
}
.slide-enter-active{
  transition: all .3s ease;
}
.slide-leave-active{
  transition: all .3s ease;
}
.slide-leave-to{
  transform:translateX(-400px);
}

.slide2-enter{
  opacity:0;
}
.slide2-enter-active{
  transition: all .6s ease;
}
.slide2-leave-active{
  transition: all .6s ease;
}
.slide2-leave-to{
  opacity:0;
}
</style>



