<template>
  <div class="music-list">
    <div class="back-header back-header-music">
      <div class="back-header-all back-header-l" @click="back"><i class="el-icon-arrow-left"></i></div>
      <div class="back-header-all back-header-m">{{ title||$store.state.musicTitle}}</div>
      <div class="back-header-all back-header-r"></div>
    </div>
		<keep-alive>
      <div @scroll="srcollEvent" class="music-l">
        <ul @click="musicDetail">
          <li class="flex-c" v-for="item in data" :data-id="item.song_id||''" :key="item.song_id||''">
            <div class="flex-i flex-i1">
              <img :src="item.pic_small">
            </div>
            <p class="flex-i flex-i2" :title="item.title">{{ formateTitle(item.title) }}&nbsp;&nbsp;
              <span>{{item.author}} &nbsp;&nbsp;{{ duration(item.file_duration) }}</span>
            </p>
          </li>
        </ul>
        <div class="load-more"  v-if="isShowMore" v-loading="loadMore"></div>
      </div>
    </keep-alive>
	</div>
</template>

<script>
import _ from "underscore"
import backHead from './backHead'
export default {
	components:{
		backHead
	},
  name: 'music',
  data:function(){
    return {
    	data:[],
      loadMore:true,
      isShowMore:false
    }
  },
  computed:{
  	title:function(){
  		let data = this.$store.state.music,
  				i=0,
  				val=this.$route.params.id;
  		for(let i=0;i<data.length;i++){
  			if(val === data[i].id) return data[i].alias
  		}
  	}
  },
  methods:{
    srcollEvent: _.throttle(function(event){
      this.isBottom()
    },1000),
    isBottom:function(){
      this.isShowMore = true
      let el = document.querySelector(".music-l")
      let elScrollTop = el.scrollTop
      let elScrollHeight = el.scrollHeight
      let elClientHeight = el.clientHeight
      if(elScrollHeight < (elScrollTop+elClientHeight+80)){
        this.getData()
      }
    },
    getData:function(){
      this.$http({
        url:API_PROXY+'http://tingapi.ting.baidu.com/v1/restserver/ting?method=baidu.ting.billboard.billList&type='+this.$route.params.id+'&size=10&offset='+this.data.length,
        method:'get'
      }).then(function(response){
          this.data = this.data.concat(response.data.song_list)
          this.isOpacity = 1
          this.isBottom()
          this.isShowMore = false
        }.bind(this))
        .catch(function(error){
          console.log(error)
        })
    },
    reset:function(){
    	this.data = [];
    	this.getData()
    },
    duration:function(val){
      if(!val) return "未知";
      return parseInt(val/60)+":"+(val%60);
    },
    formateTitle:function(val){
      if(val.length > 6){
        return val.slice(0,5)+"..."
      }
      return val
    },
    musicDetail:function(e){
      let target = e.target;
      while(target.tagName !== "LI"){
        if(!target.parentNode || target.tagName === "UL") return
        target = target.parentNode
      }
      //console.log(target.getAttribute("data-id"))
      this.$router.push({name:"musicDetail",params:{song_id:target.getAttribute("data-id"),title:this.title}})
      this.$store.commit("musicList",this.data)
    },
    back:function(){
      this.$router.go(-1)
    }
  },
  activated:function(){
    let musicListId = this.$store.state.musicListId,
        routeId = this.$route.params.id;
    this.$store.commit("musicListId",routeId);
  	if(routeId !== musicListId) this.reset()
  }
}
</script>

<style scoped>
  .load-more{
    min-height:80px;
  }
  .back-header{
    height:50px;
    background-color:#4c7be3;
    font-size:0;
    line-height:50px;
  }
  .back-header-all{
    font-size: 18px;
    color:white;
    vertical-align:middle;
    display:inline-block;
    width:20%;
    box-sizing:border-box;
  }
  .back-header-m{
    text-align:center;
    width:60%;
  }
  .back-header-music{
    position:fixed;
    width:100%;
    top:0;left:0;
  }
	.music-l{
    position: absolute;
		overflow-y:scroll;
    -webkit-overflow-scrolling:touch;
    top:50px;bottom:0;
    width:100%;
	}
  .music-l::-webkit-scrollbar{
    display: none;
  }
  .flex-c{
    display:flex;
    align-items:center;
    margin-top:20px;
  }
  .flex-i1{
    width:20%;
  }
  .flex-i1 img{
    display:block;
    width:50%;
    border-radius:50%;
    margin:0 auto;
  }
  .flex-i2{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width:80%;
    text-align:left;
    color: black;
    font-size:18px;
  }
  .flex-i2 span{
    font-size:14px;
    color: silver;
  }
</style>



