<template>
  <div class="back-header">
    <div class="back-header-all back-header-l" @click="back"><i class="el-icon-arrow-left"></i></div>
    <div class="back-header-all back-header-m">{{ title }}</div>
    <div class="back-header-all back-header-r"></div>
  </div>
</template>
<script>
export default {
  props:{
    title:{
      type:String
    }
  },
  methods:{
    back:function(){
      this.$router.go(-1);
      this.$emit("refresh")
    }
  }
}
</script>
<style scoped>
  .back-header{
    z-index: 1;
    position: fixed;
    width: 100%;
    height:50px;
    background-color:#4c7be3;
    font-size:0;
    line-height:50px;
  }
  .back-header-all{
    font-size: 18px;
    color:white;
    vertical-align:middle;
    display:inline-block;
    width:20%;
    box-sizing:border-box;
  }
  .back-header-l{
    text-align:left;
    text-indent:1em;
  }
  .back-header-m{
    text-align:center;
    width:60%;
  }
</style>






