<template>
  <div class="home">
    <div class="header">
      <div @click="toggleSlide" class="header-side slide-bar">
        <div class="slide-btn">
          <img width="30" src="../assets/images/view-list.svg">
        </div>
      </div>
      <div class="header-side title">{{ title }}</div>
      <div class="header-side"></div>
    </div>
    <navigation></navigation>
    <keep-alive>
      <router-view></router-view>
    </keep-alive>
    <slide @closeSlide="toggleSlide" :slideShow="slideShow"></slide>
  </div>
</template>

<script>
  import navigation from './Nav.vue'
  import slide from './Slide.vue'

export default {
  name: 'home',
  components:{
    navigation, slide
  },
  data:function(){
    return {
    	slideShow:false
    }
  },
  computed:{
    title:function(){
      let titleVal = this.$route.path.slice(1)
      let navs =  {
        "movie":"Movie",
        "music":"Music",
        "book":"Book",
        "picture":"Photo"
      }
      return navs[titleVal]
    }
  },
  methods:{
    toggleSlide:function(){
      this.slideShow = !this.slideShow
    }
   }
}
</script>

<style scoped>
.header{
 box-sizing:border-box;
 font-size:0;
 line-height:50px;
 background:#4c7be3;
 color:white;
 height:50px;
 position:fixed;
 width:100%;
 top:0;
}
.header-side{
  display:inline-block;
  width:20%;
  box-sizing:border-box;
  font-size:20px;
}
.slide-bar{
  width: 75px;
  height: 50px;
  float: left;
}
.title{
  text-align: center;
  font-size: 18px;
}
.slide-btn{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>



