<template>
  <div class="music-detail">
    <div class="back-header back-header-music">
      <div class="back-header-all back-header-l" @click="back"><i class="el-icon-arrow-left"></i></div>
      <div class="back-header-all back-header-m">{{ data.data.songinfo.title }}</div>
      <div class="back-header-all back-header-r"></div>
    </div>
    <div id="lrcBox">
      <div id="lrcs" v-html="lrcHtml"></div>
      <audio @timeupdate="timeUpdate" id="audio" :src="data.data.bitrate.show_link"></audio>
    </div>
    <div class="controls">
      <div class="controls-btns">
        <div class="btn btn-last"><div @click="preSong" class="el-icon-d-arrow-left"></div></div>
        <div class="btn btn-play"><div @click="isPlay" :class="{'play':!play,'pause':play}"></div></div>
        <div class="btn btn next"><div @click="nextSong" class="el-icon-d-arrow-right"></div></div>
      </div>
    </div>
  </div>
</template>
<script>
  import backHead from './backHead'
export default {
  components:{
  	backHead
  },
  data:function(){
    return {
      data:{
        data:{
          songinfo:{
            title:""
          },
          bitrate:{
            show_link:null
          }
        },
        lrc:null
      },
      lrcHtml:"",
      lrcArr:[],
      forIndex:0,
      play:false
    }
  },
  computed:{
    AudioEl:function(){
      return document.getElementById("audio")
    }
  },
  methods:{
    getSongSource:function(songId){
      this.$http({
        url:API_PROXY+'http://tingapi.ting.baidu.com/v1/restserver/ting?method=baidu.ting.song.play&songid=' + songId,
        method:'get'
      }).then(function(response){
          this.data.data = response.data
        }.bind(this))
        .catch(function(error){
          console.log(error)
        });
      this.$http({
        url:API_PROXY+'http://tingapi.ting.baidu.com/v1/restserver/ting?method=baidu.ting.song.lry&songid=' + songId,
        method:'get'
      }).then(function(response){
          this.data.lrc = response.data.lrcContent
          this.lrcHtml = ""
          this.parseLrcHtml()
        }.bind(this))
        .catch(function(error){
          console.log(error)
        })
    },
    parseLrcHtml:function(){
      if(!this.data.lrc) return
      let arr = this.filterLrc(this.data.lrc.split(/\n/)),
          i = 0, str='';
      console.log(arr)
      for(i;i<arr.length;i++){
        str = arr[i]
        this.lrcArr.push(parseFloat((parseInt(str.slice(1,3))*60+parseFloat(str.slice(4,9))).toFixed(2)))
        this.lrcHtml = this.lrcHtml + '<p class="lrc-p" id="lrc'+i+'">'+arr[i].slice(0)+'</p>'
      }
      console.log(this.lrcArr)
    },
    timeUpdate:function(event){
      if(this.ended){//播放结束
        this.resetStatus()
      }
      let curTime = event.target.currentTime
      for(let i=0;i<this.lrcArr.length;i++){
        if(curTime <= this.lrcArr[i] && curTime <= this.lrcArr[i+1]){
          this.forIndex = i;
          if(this.forIndex === i){
            this.forIndex = i+1
            let curI = i-1
            let el = document.getElementById("lrc"+curI)
            if(el){
              try{
                let elActive = document.querySelector(".lrcActive")
                if(el ===  elActive) return
                let elId = elActive.id.match(/\d+/)[0]
                elActive.classList.remove("lrcActive")
              }catch(e){}
              document.getElementById("lrcBox").scrollTop = el.offsetTop-100;
              el.classList.add("lrcActive")
              break
            }
          }
        }
      }
    },
    filterLrc:function(arr){
      return arr.filter(function(item){
        return /\[\d{2}\:\d{2}\.\d{2}\]/.test(item)
      })
    },
    back:function(){
      this.$store.state.musicTitle=this.$route.params.title;
      this.$router.go(-1);
      this.$destroy()
    },
    resetStatus:function(){
      this.forIndex = 0;
      this.lrcHtml = "";
      this.lrcArr = [];
      setTimeout(function(){
        document.getElementById("lrcBox").scrollTop = 0
      },100)
    },
    isPlay:function(){
      if(this.AudioEl.paused){
        this.play=true;
        this.AudioEl.play()
      }else{
        this.play=false;
        this.AudioEl.pause()
      }
    },
    preSong:function(){

    },
    nextSong:function(){

    }
  },
  activated:function(){
    this.getSongSource(this.$route.params.song_id)
  }
}
</script>
<style scoped>
  #app{height:100%;width:100%;}
  .music-detail{
    width:100%;min-height:100%;
  }
  .back-header-music{
    position:fixed;
    width:100%;
    display: flex;
    font-size: 18px;
  }
  .back-header-music>div{
    line-height: 50px;
  }
  .back-header-l{
    flex: 1;
  }
  .back-header-m{
    flex: 3;
  }
  .back-header-r{
    flex: 1;
  }
  .controls{
    position: fixed;
    width:100%;left:0;bottom:0;height:70px;
  }
  .controls-btns{
    display: flex;
    width: 100%;
    height: 100%;
  }
  .btn{
    flex:1;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
  }
  .btn-play>div{
    background-size: cover;
    width: 30px;
    height: 30px;
  }
  .play{background: url("../assets/images/play.png") no-repeat;}
  .pause{background: url("../assets/images/pause.png") no-repeat}
</style>



