<template>
  <div class="nav">
		<div class="nav-items">
        <router-link v-for="(nav,index) in navs" :key="index" :to="nav.name"  class="nav-item">
          <div>
            <p>{{ nav.alias }}</p>
          </div>
        </router-link>
		</div>
  </div>
</template>

<script>
export default {
  name: 'nav',
  data:function() {
    return {
      navs: [
        {name: "movie", alias: "Movie"},
        {name: "music", alias: "Music"},
        {name: "book", alias: "Book"},
        {name: "picture", alias: "Photo"}
      ]
    }
  }
}
</script>

<style scoped>
	.nav{
		position:fixed;
		bottom:0;
		width:100%;
	}
	.nav-items{
    display: flex;
    width: 100%;
	}
	.nav-item{
    transition: all .3s linear;
    background-color:white;
    color:#4c7be3;
    font-size:16px;
		line-height:50px;
	}
  a{
    flex: 1;
  }
  .active{
    color:white;
    background-color:#4c7be3;
  }
	a:hover, a:visited, a:link, a:active, a:focus {
  	text-decoration:none;outline:none;
	}
</style>



